<template>
	<div class="myBody">
		<TitleBig>
			<span slot="title"></span>
		</TitleBig>
		<div class="contentBody">
			<div class="pieChart" id="yqysLine"></div>
		</div>

	</div>
</template>

<script>
import TitleBig from "../../TitleBig.vue"
import {
	yqysLine
} from './getOption';
export default {
	components: {
		TitleBig,
	},

	data() {
		return {
			lineData: [{
				name: '1',
				value: 5
			}, {
				name: '2',
				value: 12
			}, {
				name: '3',
				value: 10
			}, {
				name: '4',
				value: 30
			}, {
				name: '5',
				value: 5
			}, {
				name: '6',
				value: 10
			}, {
				name: '7',
				value: 20
			}, {
				name: '8',
				value: 20
			}, {
				name: '9',
				value: 15
			}, {
				name: '10',
				value: 14
			}, {
				name: '11',
				value: 6
			}, {
				name: '12',
				value: 20
			}]
		};
	},
	created() {


	},

	mounted() {
		this.getEchart()
	},
	methods: {
		getEchart() {
			let chartDom = document.getElementById('yqysLine');
			this.$echarts.init(chartDom).dispose()
			let myChart = this.$echarts.init(chartDom, 'dark');
			let option = yqysLine(this.lineData)
			myChart.setOption(option);
		},
	},


}
</script>

<style lang="less" scoped>
@import "../../styles.less";

.pieChart {
	height: 21vh;
}
</style>
